<template>
  <div class="com-container">
    <div id="com-age2" class="age2"></div>
    <div id="com-age1" class="age1"></div>
  </div>
</template>

<script>
	import sd from "../../assets/js/data/age.js"
export default {
  mounted() {
    this.drawChart();
  },
  methods: {
    drawChart() {
      var myChart1 = this.$echarts.init(document.getElementById("com-age1"));
	 
var jsonData = require('../../assets/js/data/age.js');
var data=jsonData.age;
     let color = [
         "#0090FF",
         "#36CE9E",
         "#FFC005",
         "#FF515A",
         "#8B5CFF",
         "#00CA69"
     ];
     let echartData = [{
             name: "1",
             value1: 100,
             value2: 233
         },
         {
             name: "2",
             value1: 138,
             value2: 233
         },
         {
             name: "3",
             value1: 350,
             value2: 200
         },
         {
             name: "4",
             value1: 173,
             value2: 180
         },
         {
             name: "5",
             value1: 180,
             value2: 199
         },
         {
             name: "6",
             value1: 150,
             value2: 233
         },
         {
             name: "7",
             value1: 180,
             value2: 210
         },
         {
             name: "8",
             value1: 230,
             value2: 180
         }
     ];
     
     let xAxisData = data.map(item=>item.age);
     
     let yAxisData1 =data.map(item=>item.count);
    
     const hexToRgba = (hex, opacity) => {
         let rgbaColor = "";
         let reg = /^#[\da-f]{6}$/i;
         if (reg.test(hex)) {
             rgbaColor = `rgba(${parseInt("0x" + hex.slice(1, 3))},${parseInt(
           "0x" + hex.slice(3, 5)
         )},${parseInt("0x" + hex.slice(5, 7))},${opacity})`;
         }
         return rgbaColor;
     }
     
     var option1 = {
         // backgroundColor: bgColor,
         color: color,
         // legend: {
         //   right: 10,
         //   top: 10
         // },
		 // title:{
			//  text:'年龄结构',
			//  textStyle:{
			// 	  color:'white',
			//  },
			
			//  x:'center'
		 // },
         tooltip: {
             trigger: "axis",
             formatter: function(params) {
                 let html = '';
                 params.forEach(v => {
                     html += `<div style="color: #666;font-size: 14px;line-height: 24px">
                     <span style="display:inline-block;margin-right:5px;border-radius:10px;width:10px;height:10px;background-color:${color[v.componentIndex]};"></span>
                     ${v.seriesName}.${v.name}
                     <span style="color:${color[v.componentIndex]};font-weight:700;font-size: 18px">${v.value}</span>
                     人`;
                 })
                 
         
     
                 return html
             },
             extraCssText: 'background: #fff; border-radius: 0;box-shadow: 0 0 3px rgba(0, 0, 0, 0.2);color: #333;',
            
         },
         grid: {
              // top:50,
			  botton:50,
			 width:300,
			 height:175,
			 x:20,
			 x2:10,
			y2:10,
             containLabel: true
         },
		 dataZoom: [
		         {
		                    type: 'inside',
		                    xAxisIndex: [0],
		 				     // filterMode: 'filter', 
		                    start:0,
		                    end: 100
		                },
		                {
		                    type: 'inside',
		                    yAxisIndex: [0],
		 				    filterMode: 'empty',
		                    start:0,
		                    end:100
		                }
		    ],
         xAxis: [{
             type: "category",
             boundaryGap: false,
             axisLabel: {
                 formatter: '{value}岁',
                 textStyle: {
                     color: "#ffffff"
                 }
             },
             axisLine: {
                 lineStyle: {
                     color: "#ffffff"
                 }
             },
             data: xAxisData
         }],
         yAxis: [{
             type: "value",
             // name: '单位：人',
             axisLabel: {
                 textStyle: {
                     color: "#ffffff"
                 }
             },
             nameTextStyle: {
                 color: "#ffffff",
                 fontSize: 12,
                 lineHeight: 60
             },
             splitLine: {
                 lineStyle: {
                     type: "dashed",
                     color: "#ffffff"
                 }
             },
             axisLine: {
                 show: false
             },
             axisTick: {
                 show: false
             }
         }],
         series: [ {
             name: "age",
             type: "line",
             smooth: true,
             // showSymbol: false,
             symbolSize: 8,
             zlevel: 5,
             lineStyle: {
                 normal: {
                     color: color[1],
                     shadowBlur: 3,
                     shadowColor: hexToRgba(color[1], 0.5),
                     shadowOffsetY: 8
                 }
             },
             areaStyle: {
                 normal: {
                     color: new this.$echarts.graphic.LinearGradient(
                         0,
                         0,
                         0,
                         1,
                         [{
                                 offset: 0,
                                 color: hexToRgba(color[1], 0.3)
                             },
                             {
                                 offset: 1,
                                 color: hexToRgba(color[1], 0.1)
                             }
                         ],
                         false
                     ),
                     shadowColor: hexToRgba(color[1], 0.1),
                     shadowBlur: 10
                 }
             },
             data: yAxisData1
         }]
     };

      myChart1.setOption(option1);
	
	
      var myChart2 = this.$echarts.init(document.getElementById("com-age2"));
     
        // var img = 'data:image/png;base64,';
	  
	 
	  //职称结构取数
	  var getname = [ '少年','中年','青年', '老年' ];
	  var getvalue = [280 , 13336,69218,215 ];
	  var data = [];
	  for (var i = 0; i < getname.length; i++) {
	      data.push({
	          name: getname[i],
	          value: getvalue[i]
	      })
	  }
	  var colorList = ['#503EFF', '#3E82FF', '#8BF39A', '#00FCFD'];
	  var rich = {
	      name: {
	          color: "#FFF",
	          fontSize: 15,
	          padding: [10, 15, 0, 10],
	          fontWeight: '400',
	          align: 'left',
			  fontFamily: '黑体'
	      },
	      value: {
	          color: "#FFF",
	          fontSize: 15,
	          padding: [10, 10, 0, 15],
	          fontWeight: '500',
	          align: 'right'
	      },
	      percent: {
	          color: "#FFF",
	          align: 'right',
	          fontSize: 15,
	          fontWeight: '500',
	          //padding: [0, 5]
	      },
	      hr: {
	          width: '100%',
	          height: 0,
	      },
	      cir: {
	          fontSize: 26,
	      }
	  }
	  //职称结构图表
	  
	  var option2 = {
	      // backgroundColor: '#0A1934',
		  title:{
		  			 text:'年龄结构',
		  			 textStyle:{
		  				  color:'white',
						  fontSize:20
		  			 },
		  			y:'bottom',
		  			 x:'center'
		  },
	      tooltip: {
	          trigger: 'axis',
	      },
		  grid:{
			  x2:20,
			  y2:40,
			  top:40
		  },
	      series: [{
	          tooltip: {
	              trigger: 'item',
	              formatter: function(params) {
	                  return params.name + '：' + params.value + '人<br>占比：' + params.percent.toFixed(2) + '%'
	              }
	          },
	          itemStyle: {
	              normal: {
	                  borderColor: '#0A1934',
	                  borderWidth: 3,
	                  color: function(params) {
	                      return colorList[params.dataIndex]
	                  }
	              }
	          },
	          type: 'pie',
	          radius: ['30%', '60%'],
	          center: ["50%", "50%"],
	          label: {
	              normal: {
	                  show: false,
	                  position: 'inner',
	                  formatter: params => {
	                      return (
	                          '{percent|' + params.percent.toFixed(0) + '%}'
	                      );
	                  },
	                  rich: rich,
	              }
	          },
	          data: data
	      }, {
	          itemStyle: {
	              normal: {
	                  borderColor: '#0A1934',
	                  borderWidth: 3,
	                  color: function(params) {
	                      return colorList[params.dataIndex]
	                  }
	              }
	          },
	          type: 'pie',
	          silent: true, //取消高亮
	          radius: ['30%', '50%'],
	          center: ["50%", "50%"],
	          labelLine: {
	              normal: {
	                  length: 10,
	                  length2: 0,
	                  lineStyle: {
	                      color: 'transparent'
	                  }
	              }
	          },
	          label: {
	              normal: {
	  
	                  formatter: params => {
						   return '{name|' + params.name  + params.value + '}\n{hr|————————}';
	                      // return '{name|' + params.name + '}{value|' + params.value + '}\n{hr|————————}';
	                  },
	                  rich: rich,
	                  padding: [-10, 20, 0, 25]
	              }
	          },
	          data: data,
	          z: -1
	      }, {
	          itemStyle: {
	              normal: {
	                  borderColor: '#0A1934',
	                  borderWidth: 3,
	                  color: function(params) {
	                      return colorList[params.dataIndex]
	                  }
	              }
	          },
	          type: 'pie',
	          silent: true, //取消高亮
	          radius: ['30%', '50%'],
	          center: ["50%", "50%"],
	          labelLine: {
	              normal: {
	                  length: 10,
	                  length2: 0,
	                  lineStyle: {
	                      color: 'transparent'
	                  }
	              }
	          },
	          label: {
	              normal: {
	                  formatter: params => {
	                      return '\n{cir|●}\n';
	                  },
	                  rich: rich,
	              }
	          },
	          data: data,
	          z: -1
	      }]
	  };
      myChart2.setOption(option2);
	  window.addEventListener("resize", function () {
	                 	myChart1.resize();
	  					myChart2.resize();
	    });
	  
	           
    },
  },
};
</script>
<style>
.com-container {
  float: float;
}

.age1 {
  height: 50%;
  width: 100%;
}

.age2 {
  height: 48%;
  width: 100%;
  margin-top: 2%;
}
</style>
